@font-face {
  font-family: "Open Sans";
  font-weight: 300;
  font-style: normal;
  src: url("../fonts/opensanslight.woff") format("woff"), url("../fonts/opensanslight.woff2") format("woff2");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/opensans.woff") format("woff"), url("../fonts/opensans.woff2") format("woff2");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/opensansbold.woff") format("woff"), url("../fonts/opensansbold.woff2") format("woff2");
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: normal;
  font-style: normal;
  color: #000000;
  background-color: #ffffff;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  display: inline-block;
  padding: 15px 35px;
  font-size: 18px;
  line-height: 18px;
  font-weight: bold;
  color: #ffffff;
  border: 3px solid #ffffff;
  border-radius: 40px;
}
.button:hover,
.button:focus {
  background-color: #ffffff;
  color: #d22856;
}
.button:active {
  color: rgba(210, 40, 86, 0.3);
}
.page-header {
  background-color: #293746;
}
.main-nav {
  width: auto;
}
.main-nav__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.main-nav__wrapper-header {
  padding: 22px 20px;
  background-color: #283645;
  border-bottom: 1px solid #000000;
}
.main-nav__logo-icon--tablet,
.main-nav__logo-icon--desktop {
  display: none;
}
.main-nav__toggle {
  position: absolute;
  top: 22px;
  right: 20px;
  z-index: 2;
  width: 50px;
  height: 24px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 1079px) {
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 4px;
    background-color: #ffffff;
    box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
  }
  .main-nav--closed .main-nav__toggle:hover::before,
  .main-nav--closed .main-nav__toggle:focus::before {
    background-color: #d22856;
    box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
  }
  .main-nav--closed .main-nav__toggle:active::before {
    opacity: 0.3;
  }
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--closed .main-nav__wrapper-header {
    background-color: rgba(0, 0, 0, 0.3);
    border-bottom: none;
  }
  .main-nav--opened .main-nav__toggle {
    width: 25px;
    height: 25px;
  }
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 10px;
    right: -2px;
    width: 29px;
    height: 4px;
    background-color: #ffffff;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
  .main-nav--opened .main-nav__toggle:hover::before,
  .main-nav--opened .main-nav__toggle:hover::after,
  .main-nav--opened .main-nav__toggle:focus::before,
  .main-nav--opened .main-nav__toggle:focus::after {
    background-color: #d22856;
  }
  .main-nav--opened .main-nav__toggle:active::before,
  .main-nav--opened .main-nav__toggle:active::after {
    opacity: 0.3;
  }
  .main-nav--nojs .main-nav__list {
    display: block;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper-header {
    background-color: #283645;
    border-bottom: 1px solid #000000;
  }
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 42px;
  }
  .main-nav__wrapper-header {
    padding: 27px 20px;
  }
  .main-nav__logo-icon {
    width: 146.3px;
    height: 40.375px;
    fill: #ffffff;
  }
  .main-nav__logo-icon:hover,
  .main-nav__logo-icon:focus {
    fill: #d22856;
  }
  .main-nav__logo-icon:active {
    fill: rgba(255, 255, 255, 0.3);
  }
  .main-nav__logo-icon--tablet {
    display: block;
  }
  .main-nav__logo-icon--mobile {
    display: none;
  }
}
@media (min-width: 1080px) {
  .main-nav {
    background-color: rgba(0, 0, 0, 0.3);
  }
  .main-nav__wrapper {
    flex-direction: row;
    width: 940px;
    margin: 0 auto;
  }
  .main-nav__wrapper-header {
    background-color: transparent;
    border: none;
  }
  .main-nav__logo-icon {
    width: 146.3px;
    height: 40.375px;
  }
  .main-nav__logo-icon--desktop {
    display: block;
  }
  .main-nav__logo-icon--tablet {
    display: none;
  }
  .main-nav__toggle {
    display: none;
  }
}
.site-list {
  font-weight: bold;
}
.site-list a {
  display: block;
  padding: 18px 0;
  text-align: center;
  font-size: 18px;
  line-height: 32px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #283645;
  border-top: 1px solid #485461;
  border-bottom: 1px solid #000000;
}
.site-list a[href]:hover,
.site-list a[href]:focus {
  color: #d22856;
}
.site-list a[href]:active {
  color: rgba(210, 40, 86, 0.3);
}
@media (min-width: 1080px) {
  .site-list {
    font-weight: 400;
  }
  .site-list__item {
    position: relative;
    display: inline-block;
    margin-right: 80px;
    padding: 45px 0;
  }
  .site-list__item:last-child {
    margin-right: 0;
  }
  .site-list__item--active::after {
    content: "";
    position: absolute;
    bottom: 30px;
    left: 1px;
    right: 1px;
    z-index: 2;
    height: 2px;
    background-color: #ffffff;
  }
  .site-list a {
    padding: 0;
    line-height: 18px;
    text-transform: none;
    border: none;
    background-color: transparent;
  }
  .site-list a[href]:active {
    color: rgba(250, 250, 250, 0.3);
  }
}
